<template>
    <div class='pos'>
        <el-row>
            <el-col :span="7" class="pos-order" id="orderHeiht">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table border style="width: 100%" :data="tableData">
                            <el-table-column prop="goodsName" label="商品名称" align='center'></el-table-column>
                            <el-table-column prop="count" label="数量" width="50" align='center'></el-table-column>
                            <el-table-column prop="price" label="金额" width="70" align='center'></el-table-column>
                            <el-table-column label="操作" width="100" fixed="right" align='center'>
                                <template slot-scope="scope">
                                    <el-button type="text" size="small" @click="deleteSingleGoods(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="totalDiv">
                            <small>数量:</small> {{totalCount}} &nbsp;&nbsp;
                            <small>价格:</small> {{totalPrice}} 元
                        </div>
                        <div class="div-btn">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger" @click="delAllGoods">删除</el-button>
                            <el-button type="success" @click="checkOut">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span='17'>
                <div>
                    <div class="title" align='left'>常用商品</div>
                    <div class="often-goods-list">
                        <ul>
                            <li v-for="(goods,index) in oftenGoods" :key="index" @click="addOrderList(goods)">
                                <span>{{goods.goodsName}}</span>
                                <span class="o-price">¥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="good-type">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            <ul class="cookList">
                                <li v-for="(goods,index) in type0Goods" :key="index" @click="addOrderList(goods)">
                                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                    <span class="foodName">{{goods.goodsName}}</span>
                                    <span class="foodPrice">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <ul class="cookList">
                                <li v-for="(goods,index) in type1Goods" :key="index" @click="addOrderList(goods)">
                                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                    <span class="foodName">{{goods.goodsName}}</span>
                                    <span class="foodPrice">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <ul class="cookList">
                                <li v-for="(goods,index) in type2Goods" :key="index" @click="addOrderList(goods)">
                                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                    <span class="foodName">{{goods.goodsName}}</span>
                                    <span class="foodPrice">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <ul class="cookList">
                                <li v-for="(goods,index) in type3Goods" :key="index" @click="addOrderList(goods)">
                                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                    <span class="foodName">{{goods.goodsName}}</span>
                                    <span class="foodPrice">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
   
<style scope>
.pos-order {
    background-color: #f9fafc;
    border-right: 1px solid #c0ccda;
}

.div-btn {
    margin-top: 10px;
}

.title {
    height: 20px;
    border-bottom: 1px solid #D3DCE6;
    background-color: #F9FAFC;
    padding: 10px;
}

.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #E5E9F2;
    padding: 10px;
    margin: 5px;
    background-color: #fff;
    cursor: pointer;
}

.o-price {
    color: #58B7FF;
}

.good-type {
    clear: both;
}



/*明细样式*/

.cookList li {
    list-style: none;
    width: 23%;
    border: 1px solid #E5E9F2;
    height: auto;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
    cursor: pointer;
}

.cookList li span {

    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 16px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
}

.totalDiv {
    background-color: #fff;
    padding: 10px;
    border-bottom: 1px solid #D3DCE6;
}
</style>
   
<script>
import axios from 'axios'
export default {
    data() {
        return {
            tableData: [],
            oftenGoods: [],
            type0Goods: [],
            type1Goods: [],
            type2Goods: [],
            type3Goods: [],
            totalCount: 0,
            totalPrice: 0
        }
    },
    created() {
        axios.get('http://jspang.com/DemoApi/oftenGoods.php')
            .then(res => {
                this.oftenGoods = res.data;
            }).catch(err => {
                console.log(err);
                alert('网络错误,不能访问');
            })

        axios.get('http://jspang.com/DemoApi/typeGoods.php')
            .then(res => {
                // console.log(res);
                this.type0Goods = res.data[0];
                this.type1Goods = res.data[1];
                this.type2Goods = res.data[2];
                this.type3Goods = res.data[3];
            }).catch(err => {
                console.log(err);
                alert('网络错误,不能访问');
            })
    },
    mounted() {
        let orderHeight = document.body.clientHeight;
        document.querySelector('#orderHeiht').style.height = orderHeight + "px"
    },
    methods: {
        addOrderList(goods) {
            this.totalCount = 0;
            this.totalPrice = 0;
            //先判断商品列表是否存在此商品
            let isHave = false;
            for (let i = 0, len = this.tableData.length; i < len; i++) {
                if (this.tableData[i].goodsId == goods.goodsId) {
                    isHave = true;
                    break;
                }
            }
            //根据判断值写业务逻辑
            if (isHave) {
                //改变列表中商品的数量
                let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
                arr[0].count++;
            } else {
                let newGoods = { goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1 }
                this.tableData.push(newGoods);
            }

            //计算总数量和金额
            this.getAllPrice();
        },
        deleteSingleGoods(goods) {
            this.tableData = this.tableData.filter(o => o.goodsId !== goods.goodsId);
            this.getAllPrice();
        },
        getAllPrice(goods) {
            this.totalCount = 0;
            this.totalPrice = 0;
            if (this.tableData) {
                this.tableData.forEach(ele => {
                    this.totalCount += ele.count;
                    this.totalPrice = this.totalPrice + (ele.price * ele.count);
                })
            }
        },
        delAllGoods() {
            this.tableData = [];
            this.totalCount = 0;
            this.totalPrice = 0;
        },
        checkOut() {
            if (this.totalCount) {
                this.tableData = [];
                this.totalCount = 0;
                this.totalPrice = 0;
                this.$message({
                    message: '结账成功，感谢你又为店里出了一份力!',
                    type: 'success'
                })
            }else {
                this.$message.error('不能空结。老板了解你急切的心情!')
            }
        }
    }
}
</script>


